.featured{
width: 100%;
display: flex;
justify-content: space-between;
}
.featuredItem{
flex: 1;
margin: 0px 20px;
padding: 30px;
border-radius: 10px;
cursor: pointer;
margin-top: 20px;
-webkit-box-shadow: 7px -1px 5px 8px rgba(245,245,245,1);
-moz-box-shadow: 7px -1px 5px 8px rgba(245,245,245,1);
box-shadow: 7px -1px 5px 8px rgba(245,245,245,1);
}
.featuredTitle{
font-size: 20px;
}
.featuredMoneyContainer{
margin: 10px 0px;
display: flex;
align-items: center;
}
.featuredMoney{
font-size: 30px;
font-weight: 600;
}
.featuredMoneyRate{
display: flex;
align-items: center;
margin-left: 20px;
}
.featuredIcon{
font-size: 14px;
margin-left: 5px;
color: green;
}
.featuredIcon.nagative{
color: red;
}
.featuredSub{
font-size: 16px;
color:gray;
}