.topbar{
width: 100%;
height: 60px;
background-color:midnightblue;
position: sticky;
top: 0;
z-index: 999;
}
.topbarWrapper{
height: 100%;
padding: 0px 20px;
display: flex;
justify-content: space-between;
align-items: center;

}
.logo{
font-weight: bold;
font-size: 30px;
color:white;
}
.topbarIconContainer{
position: relative;
cursor: pointer;
margin-right:10px;
color:white;
}

.topIconBadge{
width: 15px;
height: 15px;
position: absolute;
top: -5px;
right: 0px;
background-color: red;
color: white;
border-radius: 50%;
display: flex;
justify-content: center;
align-items: center;
font-size: 10px;
text-align: center;

}
.topRight{
display: flex;
}
.topAvatar{
width: 40px;
height: 40px;
border-radius: 50%;
}